
'use client';

import { useState, useEffect, useRef } from 'react';
import { onSnapshot, type DocumentReference, refEqual } from 'firebase/firestore';
import { FirestorePermissionError, errorEmitter } from '@/firebase';

interface UseDocOptions {
  // Add any options here if needed in the future
}

export function useDoc<T>(
  docRef: DocumentReference | null | undefined,
  options?: UseDocOptions
) {
  const [data, setData] = useState<T | null>(null);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState<Error | null>(null);
  
  const docRefRef = useRef<DocumentReference | null | undefined>(docRef);

  // Compare the new docRef with the stored one.
  if (docRef === null && docRefRef.current !== null) {
      docRefRef.current = null;
  } else if (docRef && (!docRefRef.current || !refEqual(docRef, docRefRef.current))) {
      docRefRef.current = docRef;
  }


  useEffect(() => {
    // If the docRef is not ready, don't do anything.
    if (!docRefRef.current) {
      setData(null);
      setIsLoading(false);
      return;
    }
    setIsLoading(true);

    const unsubscribe = onSnapshot(
      docRefRef.current,
      (snapshot) => {
        if (snapshot.exists()) {
           const docData = snapshot.data();
           // Convert Firestore Timestamps to JS Dates
           Object.keys(docData).forEach(key => {
            if (docData[key]?.toDate && typeof docData[key].toDate === 'function') {
              docData[key] = docData[key].toDate();
            }
          });
          setData({ id: snapshot.id, ...docData } as unknown as T);
        } else {
          setData(null);
        }
        setIsLoading(false);
        setError(null);
      },
      async (err) => {
        setError(err);
        setIsLoading(false);
        if (err.code === 'permission-denied' && docRefRef.current) {
            const permissionError = new FirestorePermissionError({
              path: docRefRef.current.path,
              operation: 'get',
            });
            errorEmitter.emit('permission-error', permissionError);
        }
      }
    );

    return () => unsubscribe();
  }, [docRefRef.current]); // Re-run effect only when the memoized doc reference changes.

  return { data, isLoading, error };
}
