
'use client';

import { useState, useEffect, useRef } from 'react';
import {
  onSnapshot,
  queryEqual,
  type Query,
} from 'firebase/firestore';
import { FirestorePermissionError, errorEmitter } from '@/firebase';

interface UseCollectionOptions {
  // Add any options here if needed in the future
}

export function useCollection<T>(
  query: Query | null | undefined,
  options?: UseCollectionOptions
) {
  const [data, setData] = useState<T[] | null>(null);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState<Error | null>(null);

  const queryRef = useRef<Query | null | undefined>(query);

  // Compare the new query with the stored one.
  if (query === null && queryRef.current !== null) {
    queryRef.current = null;
  } else if (query && (!queryRef.current || !queryEqual(query, queryRef.current))) {
    queryRef.current = query;
  }


  useEffect(() => {
    // If the query is not ready, don't do anything.
    if (!queryRef.current) {
      setData(null);
      setIsLoading(false);
      return;
    }
    
    setIsLoading(true);

    const unsubscribe = onSnapshot(
      queryRef.current,
      (snapshot) => {
        const result: T[] = [];
        snapshot.forEach((doc) => {
          const docData = doc.data();
          // Convert Firestore Timestamps to JS Dates
          Object.keys(docData).forEach(key => {
            if (docData[key]?.toDate && typeof docData[key].toDate === 'function') {
              docData[key] = docData[key].toDate();
            }
          });
          result.push({ id: doc.id, ...docData } as unknown as T);
        });
        setData(result);
        setIsLoading(false);
        setError(null);
      },
      async (err) => {
        setError(err);
        setIsLoading(false);
        // Only create and emit error if the query is valid
        if (err.code === 'permission-denied' && queryRef.current) {
            const permissionError = new FirestorePermissionError({
              path: queryRef.current.path,
              operation: 'list',
            });
            errorEmitter.emit('permission-error', permissionError);
        }
      }
    );

    return () => unsubscribe();
  }, [queryRef.current]); // Re-run effect only when the memoized query reference changes.

  return { data, isLoading, error };
}
